<template>
	<view class="">
		<view class="content" v-if="newsData">
			<navigator :url="`/pages/newsdetails/newsdetails?id=${item.id}`" class="news-skip" target=""
				v-for="item in newsData" :key="item.id">
				<view class="news-item">
					<view class="title">{{item.title}}</view>
					<view class="list-box">
						<image class="list-left" :src="item.titlepic" mode=""></image>
						<view class="list-right">
							<view class="list-smalltext">
								{{item.smalltext}}
							</view>
							<view class="info">
								<view class="info-left">

								</view>
								<view class="info-right">
									{{item.newstime | formatDate}}
								</view>
							</view>
						</view>
					</view>
				</view>
			</navigator>
		</view>
	</view>
</template>

<script>
	import {
		getNewsList
	} from '@/api/news.js'
	export default {
		name: "NewsList",
		data() {
			return {}
		},
		props: ['newsData'],
		filters: {
			formatDate(value) {
				if (value == undefined) {
					return;
				}
				let date = new Date(value * 1000);
				// let date = new Date(value);
				//时间戳为10位需*1000，时间戳为13位的话不需乘1000
				let y = date.getFullYear();
				let MM = date.getMonth() + 1;
				MM = MM < 10 ? ('0' + MM) : MM; //月补0
				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d; //天补0
				let h = date.getHours();
				h = h < 10 ? ('0' + h) : h; //小时补0
				let m = date.getMinutes();
				m = m < 10 ? ('0' + m) : m; //分钟补0
				let s = date.getSeconds();
				s = s < 10 ? ('0' + s) : s; //秒补0
				// return y + '-' + MM + '-' + d; //年月日
				return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s; //年月日时分秒
			}
		},
	}
</script>

<style lang="scss" scoped>
	.news-item {
		border-radius: 5px;
		background-color: #fff;
		padding: 13px 15px;
		margin-top: 8px;
		// box-shadow: 0 0px 4px rgba(0, 0, 0, 0.1);

		.title {
			font-size: 16px;
			font-weight: 700;
			color: #101010;
			margin-bottom: 8px;
		}

		.list-box {
			display: flex;

		}

		.list-left {
			flex-shrink: 0;
			width: 110px;
			height: auto;

			margin-right: 5px;
			border-radius: 3px;
		}

		.list-right {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			width: 100%;

			.list-smalltext {
				text-align: justify;
				font-size: 15px;
				color: #555;
				@include textEllipsis(3);
			}


			.info {
				display: flex;
				justify-content: space-between;
				font-size: 12px;

				.info-left {}

				.info-right {
					color: #a6a4a4;
				}
			}
		}

	}
</style>
